<template>
	<div class="timingDiv">
		<div class="wrap">
			<div class="wrap_inner">

				<div class="item" style="margin-top:0.3rem;">
					<h5>
						<img src="../static/img/dingshi.png" class="left">
						<span>定时</span>
						<img src="../static/img/guan.png" class="right timeSwitch" id="timeSwitch">
					</h5>
					<div class="left_time">
						<h6>
							<span class="hour">--</span>小时
							<span class="minute">--</span>分钟后进入如下模式</h6>
					</div>
				</div>

				<workMode></workMode>
				<timeGears></timeGears>
				<xinFengBi></xinFengBi>
				
				<div class="item timeEMF">
					<h5><img src="../static/img/emf.png" class="left">
						<span>EMF</span><img src="../static/img/guan.png" class="right timeEMF_swich"></h5>
				</div>
				<div class="timesure" @click="back()">
					确定
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import workMode from "./workMode.vue";
import timeGears from "./timegears.vue";
import xinFengBi from "./xinFengBi.vue";

export default {
  name: "timingDiv",
  components: {
	workMode,
	timeGears,
	xinFengBi
  },
  methods:{
	  back(){
		  this.$router.go(-1)
	  }
  }
};
</script>



